body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cardImage {
  width: 100%;
  height: 40vh;
  object-fit: contain;
  margin-top: 1vh;
  background-color:  #1a1a1a ;
  padding: 1.5vh;
  
}

.description {
  text-align: center;
  background-color: red;
  border-radius: 25px;
  height: 3vh;
  color: white;
}

.buttons {
  width: 100%
 
}

ul {
list-style-type: none;
}

.Jumbotron {
  color: midnightblue;
  font-weight: bolder;
  text-align: left;
  margin-top: 20px;
}

.cards {
  width: 45vh;
  min-height: 70vh;
  color: #262626;
}

.cardbody {
  background-color: rgb(224, 222, 222);
  margin-bottom: 2vh;
}
